.about {
   padding-bottom: 75px;

   &__container {
      padding-top: 77px;
      display: flex;
      align-items: flex-start;
      position: relative;
   }

   &__content {
      margin-right: 51px;
      max-width: 35.3%;
   }

   &__center {
      padding: 82px 20px 50px 70px;
      max-width: 32.5%;
      margin-top: -150px;
      position: relative;
      background: var(--color-dark);
      color: #fff;
      z-index: 2;
   }

   &__title {
      max-width: 100%;
      margin-bottom: 36px;

      &:after {
         width: 44%;
      }
   }

   &__text {
      font-weight: 500;
      font-size: 16px;
      line-height: 170%;
      color: var(--color-dark);
      margin-bottom: 46px;
   }

   &__link {
      color: var(--color-red);
      border: 2px solid var(--color-red);

      svg path {
         stroke: var(--color-red);
      }
   }

   &__scroll {
      top: 56px;
      width: 343px;

      &:before {
         content: "02";
      }
   }
}

.about-pictures {
   position: absolute;
   top: 0;
   right: 0;
   width: 53%;
   z-index: 1;

   &__item {
      position: absolute;
      z-index: 1;

      img {
         display: block;
      }

      &:nth-child(3n+1) {
         top: 0;
         right: 70px;
         z-index: 3;
         border-bottom: 5px solid #fff;
         border-left: 5px solid #fff;
      }

      &:nth-child(3n+2) {
         border-top: 5px solid #fff;
         border-left: 5px solid #fff;
         top: auto;
         bottom: -625px;
         right: 70px;
         z-index: 2;
      }

      &:nth-child(3n+3) {
         border-top: 5px solid #fff;
         border-right: 5px solid #fff;
         top: auto;
         bottom: -625px;
         left: -77px;
         z-index: 1;
      }

      &:nth-child(n+4) {
         z-index: 0;
      }

      &:nth-child(3n+1).active {
         animation: tilt-in-top-1 0.6s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
         z-index: 3;
      }

      &:nth-child(3n+2).active {
         animation: tilt-in-right-2 0.6s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
         z-index: 3;
      }

      &:nth-child(3n+3).active {
         animation: tilt-in-bottom-1 0.6s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
         z-index: 3;
      }

   }
}

@-webkit-keyframes tilt-in-right-2 {
   0% {
      -webkit-transform: rotateX(30deg) translateX(300px) skewX(-30deg);
      transform: rotateX(30deg) translateX(300px) skewX(-30deg);
      opacity: 0;
   }

   100% {
      -webkit-transform: rotateX(0deg) translateX(0) skewX(0deg);
      transform: rotateX(0deg) translateX(0) skewX(0deg);
      opacity: 1;
   }
}

@keyframes tilt-in-right-2 {
   0% {
      -webkit-transform: rotateX(30deg) translateX(300px) skewX(-30deg);
      transform: rotateX(30deg) translateX(300px) skewX(-30deg);
      opacity: 0;
   }

   100% {
      -webkit-transform: rotateX(0deg) translateX(0) skewX(0deg);
      transform: rotateX(0deg) translateX(0) skewX(0deg);
      opacity: 1;
   }
}

@-webkit-keyframes tilt-in-bottom-1 {
   0% {
      -webkit-transform: rotateY(30deg) translateY(300px) skewY(-30deg);
      transform: rotateY(30deg) translateY(300px) skewY(-30deg);
      opacity: 0;
   }

   100% {
      -webkit-transform: rotateY(0deg) translateY(0) skewY(0deg);
      transform: rotateY(0deg) translateY(0) skewY(0deg);
      opacity: 1;
   }
}

@keyframes tilt-in-bottom-1 {
   0% {
      -webkit-transform: rotateY(30deg) translateY(300px) skewY(-30deg);
      transform: rotateY(30deg) translateY(300px) skewY(-30deg);
      opacity: 0;
   }

   100% {
      -webkit-transform: rotateY(0deg) translateY(0) skewY(0deg);
      transform: rotateY(0deg) translateY(0) skewY(0deg);
      opacity: 1;
   }
}

@-webkit-keyframes tilt-in-top-1 {
   0% {
      -webkit-transform: rotateY(30deg) translateY(-300px) skewY(-30deg);
      transform: rotateY(30deg) translateY(-300px) skewY(-30deg);
      opacity: 0;
   }

   100% {
      -webkit-transform: rotateY(0deg) translateY(0) skewY(0deg);
      transform: rotateY(0deg) translateY(0) skewY(0deg);
      opacity: 1;
   }
}

@keyframes tilt-in-top-1 {
   0% {
      -webkit-transform: rotateY(30deg) translateY(-300px) skewY(-30deg);
      transform: rotateY(30deg) translateY(-300px) skewY(-30deg);
      opacity: 0;
   }

   100% {
      -webkit-transform: rotateY(0deg) translateY(0) skewY(0deg);
      transform: rotateY(0deg) translateY(0) skewY(0deg);
      opacity: 1;
   }
}

.about-slider {
   &__title {
      font-weight: bold;
      font-size: 32px;
      line-height: 32px;
      text-transform: uppercase;
      color: #ffffff;
      margin-bottom: 30px;
   }

   &__text {
      font-weight: 500;
      font-size: 16px;
      line-height: 170%;
      color: #edf1f4;
      max-width: 413px;
      margin-bottom: 14px;
   }

   &__link {
      color: var(--color-red);
      margin-left: -40px;

      svg {
         margin-right: 14px;

         path {
            stroke: var(--color-red);
            fill: var(--color-red);
         }
      }
   }

   &__counter {
      top: 25px;
      left: 24px;
   }

   &__item {
      outline: none;
   }

   .slick-arrow {
      top: -60px;
      bottom: auto;
      width: 30px;
      height: 30px;
      z-index: 99;
      transform: translateX(0);
      transition: transform 0.3s;
   }

   .slick-prev {
      left: auto;
      right: 66px;
      background: url("../img/arrow-up.svg") no-repeat center center;

      &.slick-disabled {
         background: url("../img/arrow-up-disabled.svg") no-repeat center center;
      }
   }

   .slick-prev:hover {
      transform: translateY(-5px);
      transition: transform 0.3s;
   }

   .slick-next {
      left: auto;
      right: 7px;
      background: url("../img/arrow-down.svg") no-repeat center center;

      &.slick-disabled {
         background: url("../img/arrow-down-disabled.svg") no-repeat center center;
      }
   }

   .slick-next:hover {
      transform: translateY(5px);
      transition: transform 0.3s;
   }
}