:root {
  --color-dark: #434343;
  --color-bg: #e5e5e5;
  --color-gray: #959595;
  --color-red: #f33131;
}

@font-face {
  font-family: "Akrobat";
  src: url("../fonts/Akrobat-Black.woff2") format("woff2");
  font-weight: 900;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Akrobat";
  src: url("../fonts/Akrobat-ExtraBold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Akrobat";
  src: url("../fonts/Akrobat-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Akrobat";
  src: url("../fonts/Akrobat.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Open sans";
  src: url("../fonts/Open sans.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Akrobat";
  src: url("../fonts/Akrobat-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

