/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before,
.slick-track:after {
  display: table;
  content: '';
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px;
}

[dir='rtl'] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Akrobat", sans-serif;
  font-weight: 600;
  background: var(--color-bg);
  color: var(--color-dark);
}

body.lock {
  overflow: hidden;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
  outline: none;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

button {
  border: none;
  background: transparent;
  cursor: pointer;
  outline: none;
}

input {
  outline: none;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
}

.error {
  border: 2px #ff4019 solid !important;
}

.container {
  max-width: 1730px;
  padding-left: 15px;
  padding-right: 15px;
  width: 100%;
  margin: 0 auto;
  position: relative;
}

.slick-arrow {
  position: absolute;
  font-size: 0;
  background: none;
  border: none;
  cursor: pointer;
}

.slick-arrow:after, .slick-arrow:before {
  display: none;
}

.slick-initialized .slick-slide {
  min-height: 0;
}

.slick-slider {
  margin-bottom: 0;
}

.scroll {
  display: block;
  position: absolute;
  transform-origin: 100% 100%;
  transform: rotate(-90deg);
  font-weight: 600;
  font-size: 18px;
  line-height: 20px;
  letter-spacing: 0.06em;
  text-align: right;
  right: 18px;
}

.scroll:before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
}

.link {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  font-size: 18px;
  line-height: 20px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  padding: 13px 40px;
}

.link svg {
  margin-right: 10px;
}

.link svg path {
  stroke: var(--color-dark);
  fill: var(--color-dark);
}

.title {
  font-weight: 600;
  font-size: 42px;
  line-height: 130%;
  max-width: max-content;
  padding-bottom: 17px;
  text-transform: uppercase;
  color: var(--color-dark);
  position: relative;
}

.title:after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 2px;
  background: var(--color-dark);
}

.title span {
  color: var(--color-gray);
}

.counter {
  font-weight: 600;
  font-size: 22px;
  line-height: 20px;
  position: absolute;
  transform-origin: 0 0;
  text-transform: uppercase;
}

.counter__right {
  font-size: 16px;
  color: var(--color-gray);
}

.header-top {
  position: relative;
  z-index: 100;
  padding: 7px 0 11px;
  background-color: var(--color-bg);
}

.header-top__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-top__viber {
  margin-left: auto;
  margin-right: 43px;
  color: var(--color-gray);
  font-weight: 600;
  font-size: 18px;
  line-height: 18px;
  letter-spacing: 0.06em;
  transition: opacity 0.3s;
}

.header-top__viber:hover {
  opacity: 0.7;
  transition: opacity 0.3s;
}

.header-top__viber--phone {
  color: var(--color-dark);
  margin-left: 5px;
}

.header-top__phone {
  display: block;
  border: 2px solid var(--color-dark);
  padding: 8px 12px;
  padding-left: 13px;
  font-weight: 600;
  font-size: 18px;
  line-height: 18px;
  letter-spacing: 0.06em;
}

.header-top__phone svg {
  width: 14px;
  height: 15px;
  margin-right: 3px;
}

.header-top__phone:hover {
  opacity: 0.7;
  transition: opacity 0.3s;
}

.lang {
  display: flex;
}

.lang__item {
  margin-right: 10px;
  font-weight: 400;
  font-size: 18px;
  line-height: 30px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: var(--color-gray);
  transition: color 0.3s;
}

.lang__item.current-lang {
  color: var(--color-dark);
  font-weight: 600;
  border-bottom: 2px solid var(--color-dark);
}

.lang__item:hover {
  color: var(--color-dark);
  font-weight: 600;
  border-bottom: 2px solid var(--color-dark);
  transition: color 0.3s;
}

.header-bottom {
  position: relative;
  z-index: 100;
  background: #ffffff;
}

.header-bottom__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 13px;
  padding-bottom: 13px;
}

.header-bottom__left {
  display: flex;
  align-items: center;
  max-width: 454px;
  width: 100%;
  padding-bottom: 5px;
}

.header-bottom__title {
  display: flex;
  align-items: center;
  font-weight: 600;
  font-size: 22px;
  line-height: 20px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: var(--color-dark);
  margin-right: 56px;
}

.header-bottom__title.hidden {
  display: none;
}

.header-bottom__logo {
  margin-right: 284px;
}

.cabinet {
  display: flex;
  align-items: center;
}

.cabinet__link {
  display: inline-block;
  background-position: bottom center;
  background-size: cover;
  background-repeat: no-repeat;
}

.cabinet__user svg {
  width: 20px;
  height: 20px;
}

.cabinet__favorite svg {
  width: 25px;
  height: 20px;
}

.cabinet__item {
  margin-right: 48px;
}

.cabinet__item:last-child {
  margin-right: 5px;
}

.cart {
  position: relative;
}

.cart__icon {
  display: inline-block;
}

.cart__icon svg {
  width: 21px;
  height: 21px;
}

.cart__quantity {
  position: absolute;
  right: -7px;
  top: -7px;
  display: inline-flex;
  align-items: center;
  color: #fff;
  border-radius: 25px;
  height: 17px;
  padding: 0 5px;
  background: var(--color-dark);
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 12px;
  text-align: center;
}

.searh-form {
  max-width: 268px;
  width: 100%;
  display: flex;
  align-items: center;
}

.searh-form svg {
  width: 24px;
  height: 24px;
}

.searh-form__input {
  width: 100%;
  border: none;
  height: 35px;
  border-bottom: 2px solid var(--color-dark);
  font-weight: 600;
  font-size: 22px;
  line-height: 20px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: var(--color-dark);
  padding-left: 37px;
  background: url("../img/search.svg") no-repeat left center;
}

.searh-form__input:focus {
  background-image: url("../img/search-focus.svg");
  border-bottom: 2px solid var(--color-red);
}

.header-menu {
  position: absolute;
  left: 0;
  right: 0;
  background: #fff;
  box-shadow: 0px 10px 27px -13px rgba(0, 0, 0, 0.1) inset;
  padding-top: 152px;
  padding-bottom: 150px;
  transform: translateY(-130%);
  transition: transform 0.6s;
  z-index: 90;
}

.burger {
  width: 36px;
  height: 15px;
  margin-right: 16px;
  flex-shrink: 0;
  padding: 0;
  position: relative;
}

.burger span {
  height: 2px;
  width: 100%;
  background: var(--color-dark);
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  right: 0;
  opacity: 1;
  transition: opacity 0.3s;
}

.burger:before, .burger:after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  height: 2px;
  background: var(--color-dark);
  transition: transform 0.3s;
}

.burger:before {
  top: 0;
}

.burger:after {
  bottom: 0;
  width: 17px;
}

.header-nav {
  display: flex;
  justify-content: space-between;
  margin-bottom: 215px;
  padding-left: 50px;
}

.header-nav__item {
  width: 33.3%;
  padding-left: 200px;
}

.header-nav__item ul {
  font-weight: 600;
  font-size: 22px;
  line-height: 20px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
}

.header-nav__item ul li {
  position: relative;
}

.header-nav__item ul li:nth-child(3n + 1):before {
  background: url("../img/menu-1.1.svg") no-repeat center center;
}

.header-nav__item ul li:nth-child(3n + 2):before {
  background: url("../img/menu-1.2.svg") no-repeat center center;
}

.header-nav__item ul li:nth-child(3n + 3):before {
  background: url("../img/menu-1.3.svg") no-repeat center center;
}

.header-nav__item ul li:before {
  content: "";
  display: block;
  position: absolute;
  left: -50px;
  top: -8px;
  width: 32px;
  height: 32px;
}

.header-nav__item ul li:not(:last-child) {
  margin-bottom: 26px;
}

.header-nav__title {
  font-weight: 700;
  font-size: 30px;
  line-height: 32px;
  text-transform: uppercase;
  padding-bottom: 11px;
  margin-bottom: 45px;
  position: relative;
}

.header-nav__title:before {
  content: "";
  display: block;
  position: absolute;
  background: url("../img/menu-1.svg") no-repeat center center;
  left: -50px;
  top: 0;
  width: 32px;
  height: 32px;
}

.header-nav__title:after {
  width: 50%;
}

.header-nav__center .header-nav__title:before {
  background: url("../img/menu-2.svg") no-repeat center center;
}

.header-nav__center ul li:nth-child(3n + 1):before {
  background: url("../img/menu-2.1.svg") no-repeat center center;
}

.header-nav__center ul li:nth-child(3n + 2):before {
  background: url("../img/menu-2.2.svg") no-repeat center center;
}

.header-nav__center ul li:nth-child(3n + 3):before {
  background: url("../img/menu-2.3.svg") no-repeat center center;
}

.header-nav__right .header-nav__title:before {
  background: url("../img/menu-3.svg") no-repeat center center;
}

.main-menu {
  display: flex;
  justify-content: center;
  position: relative;
}

.main-menu:before {
  content: "";
  display: block;
  position: absolute;
  top: -55px;
  left: 50%;
  transform: translateX(-50%);
  max-width: 556px;
  width: 100%;
  background: var(--color-dark);
  height: 1px;
}

.main-menu li {
  font-weight: 600;
  font-size: 22px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
}

.main-menu li:not(:last-child) {
  margin-right: 67px;
}

/*  Open menu  */
.header-menu.active {
  display: block;
  transform: translateY(0);
  transition: transform 0.6s;
  height: 100%;
}

.header-menu.active .header {
  height: auto;
}

.burger.active {
  height: 30px;
  width: 30px;
}

.burger.active span {
  transform: scale(0);
}

.burger.active:before {
  top: 16px;
  left: -2px;
  transform: rotate(-45deg);
  transition: transform 0.3s;
  width: 100%;
}

.burger.active:after {
  bottom: 12px;
  transform: rotate(45deg);
  transition: transform 0.3s;
  width: 100%;
}

.first {
  padding-top: 11px;
  padding-bottom: 185px;
  background: #fff;
  box-shadow: 0px 10px 27px -13px rgba(0, 0, 0, 0.1) inset;
}

.first__bottom {
  max-width: 360px;
  margin-right: 40px;
  margin-left: auto;
}

.first__scroll {
  top: 111px;
  width: 343px;
}

.first__scroll:before {
  content: "";
  bottom: 50%;
  transform: translateY(50%);
  width: 77px;
  height: 2px;
  background: var(--color-dark);
}

.first-slider {
  padding: 0 30px;
}

.first-slider__item {
  position: relative;
  background: url("../img/first-bg.svg") no-repeat center 78%;
  min-width: 0;
}

.first-slider__item:focus {
  outline: none;
}

.first-slider__wrap {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
