@media screen and (max-width: 1440px) {
	.title {
		font-size: 38px;
	}

	.header-nav {
		padding-left: 15px;
		&__item {
			padding-left: 130px;
		}
	}

	.first-slider {
		margin-bottom: 20px;

		&__picture {
			margin-right: 0;
		}

		&__content {
			margin-left: 0;
		}
	}

	.about {
		padding-bottom: 50px;

		&__container {
			padding-top: 50px;
		}

		&__content {
			max-width: 33%;
		}

		&__picture {
			width: 56%;
		}

		&__center {
			padding-left: 50px;
		}
	}

	.category {
		padding-top: 150px;
	}

	.blog__content {
		padding-left: 0;
	}

	.blog-slider {
		&__nav {
			left: 0;
		}

		&__counter {
			left: 600px;
		}
	}

	.photo-slider {
		.slick-track {
			height: 560px;
		}

		.photo-slider__item {
			padding: 0 20px;

			img {
				height: 350px;
				width: 90%;
			}
		}
	}
}

@media screen and (max-width: 1200px) {
	.header-nav {
		&__item {
			padding-left: 70px;
			ul {
				padding-left: 0;
			}
		}
	}
	.first-slider {
		margin-top: 50px;
		margin-bottom: 50px;

		&__wrap {
			flex-direction: column;
		}

		&__content {
			padding-top: 20px;
			margin: 0 auto;
			order: 1;
		}

		&__wrap {
			padding-bottom: 100px;
		}

		&__item {
			background-position-y: 0;
		}

		&__picture {
			order: 0;
			width: 100%;

			img {
				margin: 0 auto;
				max-width: 300px;
			}
		}

		&__detail {
			bottom: 0;
			margin-left: 0;
		}

		&__counter {
			top: 350px;
			bottom: auto;
		}
	}

	.first {
		padding-bottom: 80px;
	}

	.about {
		&__container {
			padding-top: 80px;
			flex-direction: column;
		}

		&__title {
			text-align: center;
			margin-bottom: 30px;
		}

		&__content {
			text-align: center;
			max-width: 100%;
		}

		&__center {
			max-width: 40%;
			margin: 0 auto 124px 0;
		}

		&__picture {
			top: auto;
			bottom: 0;
			right: 20%;
			width: 60%;
		}

		&__link {
			margin-bottom: 50px;
		}
	}

	.about-pictures {
		left: 15px;
		top: 398px;
		width: 90%;

		&__item {
			&:nth-child(3n + 1) {
				right: 0;
			}

			&:nth-child(3n + 2) {
				right: 0;
			}

			&:nth-child(3n + 3) {
				left: 0;
			}
		}
	}

	.photo-slider {
		background-position-y: 100%;
		background-size: contain;

		.slick-track {
			height: 560px;
		}

		.photo-slider__item {
			padding: 0 30px;

			img {
				height: 350px;
				max-width: 400px;
				width: 100%;
			}
		}
	}
	.footer {
		&__container {
			display: block;
		}
		&__left {
			width: 100%;
			margin-bottom: 30px;
		}
		&-nav {
			width: 100%;
		}
		&-top {
			padding-top: 350px;
		}
		&-menu {
			width: 80%;
		}
	}
}

@media screen and (max-width: 1140px) {
	.searh-form {
		display: none;
	}

	.header-bottom {
		&__logo {
			margin-right: 0;
		}

		&__left {
			max-width: max-content;
		}

		&__title {
			margin-right: 0;
		}
	}

	.first-slider__old-price {
		margin-top: auto;
	}

	.category {
		&__list {
			display: block;
			padding-right: 0;
		}

		&-item {
			width: 100%;
			margin-bottom: 50px;

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	.services {
		padding-top: 80px;
		padding-bottom: 80px;

		&__title {
			margin-bottom: 90px;
		}

		&__scroll {
			width: 64%;
		}
	}

	.blog {
		padding-top: 80px;
	}

	.blog-slider {
		margin-right: calc((-100vw + 992px) / 2);

		&__nav-item {
			max-width: 500px;
		}

		&__counter {
			left: 530px;
		}

		.slick-next {
			left: 90%;
		}

		.slick-prev {
			left: 84%;
		}
	}
}

@media screen and (max-width: 992px) {
	.header-nav {
		display: block;
		margin-bottom: 140px;
		&__item {
			margin: 0 auto 50px;
			width: max-content;
		}
	}
	.header-menu {
		padding-top: 80px;
		padding-bottom: 80px;
	}
	.main-menu {
		width: 100%;
		justify-content: space-between;
		padding-left: 15px;
		padding-right: 15px;
		li:not(:last-child) {
			margin-right: 0;
		}
	}
	.about {
		&__picture {
			right: 0;
			width: 72%;
		}

		&__center {
			max-width: 52%;
		}

		&__scroll {
			width: 260px;
		}

		&__container {
			padding-bottom: 90px;
		}
	}

	.about-pictures {
		width: calc(100% - 30px);
		right: 15px;
		top: auto;
		bottom: 488px;
	}

	.blog-slider {
		padding-top: 60px;
		padding-left: 245px;
		margin-right: calc((-100vw + 768px) / 2);

		.slick-next {
			left: 84%;
		}

		.slick-prev {
			left: 76%;
		}

		&__counter {
			left: 440px;
		}

		&__nav img {
			object-fit: cover;
		}

		&__nav-item {
			max-width: 400px;
			height: 353px;
		}
	}

	.photo-slider {
		padding-top: 25px;

		&__counter {
			top: 140px;
			margin-left: -215px;
		}

		&__title {
			font-size: 24px;
		}

		.slick-prev {
			transform: translateX(140px);

			&:hover {
				transform: translateX(140px);
			}
		}

		.slick-next {
			transform: translateX(200px);

			&:hover {
				transform: translateX(200px);
			}
		}

		.slick-track {
			height: 440px;
		}

		.photo-slider__item {
			img {
				max-width: 300px;
				height: 250px;
			}

			&.slick-center {
				img {
					transform: scale(1.2) translateY(-20px);
				}
			}
		}

		.slick-arrow {
			top: 40px;
		}
	}
}

@media screen and (max-width: 768px) {
	.main-menu {
		padding-top: 0;
		display: block;
		&__item {
			margin-bottom: 20px;
		}
		&:before {
			display: none;
		}
	}
	.header-nav {
		margin-bottom: 0;
		&__title {
			font-size: 28px;
			margin-bottom: 25px;
		}
	}
	.header-list {
		font-size: 18px;
	}
	.first-slider {
		&__title {
			font-size: 32px;
		}

		&__text {
			font-size: 32px;
		}

		&__old-price {
			font-size: 20px;
		}

		&__new-price {
			font-size: 32px;
		}
	}

	.about {
		&__picture {
			right: 0;
			width: 72%;
		}

		&__center {
			max-width: 52%;
			padding: 65px 20px 30px 30px;
		}
	}

	.about-slider {
		&__title {
			font-size: 22px;
		}

		&__text {
			font-size: 14px;
		}

		.slick-arrow {
			top: -45px;
		}
	}

	.gallery {
		display: block;

		.gallery__item {
			width: 85%;
			margin: 0 auto;
		}
	}

	.blog {
		&__scroll {
			width: 750px;
		}
	}

	.blog-slider {
		margin-right: calc((-100vw + 530px) / 2);
		padding-top: 230px;
		padding-left: 46px;

		&__counter {
			left: 0;
			top: -40px;
		}

		.slick-prev {
			left: 68%;
		}

		.slick-next {
			left: 78%;
		}

		.slick-arrow {
			top: -45px;
		}
	}
	.footer {
		&-menu {
			display: block;
			width: 50%;
			&__item {
				margin-bottom: 30px;
			}
		}
	}
}

@media screen and (max-width: 576px) {
	.container {
		width: 100%;
	}

	.scroll {
		display: none;
	}

	.title {
		text-align: center;
		font-size: 32px;
	}

	.link {
		padding: 10px 20px;
		font-size: 16px;
	}

	.counter {
		font-size: 18px;
	}

	.slick-slider .slick-arrow {
		width: 25px;
		height: 25px;
		background-size: contain;
	}

	.header-top__viber {
		display: none;
	}

	.header-bottom {
		&__left {
			display: block;
		}
	}

	.burger {
		margin-bottom: 20px;
	}

	.cabinet {
		display: block;
		text-align: right;

		&__item {
			margin-right: 0;
			margin-bottom: 10px;

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	.first-slider {
		padding: 0;

		&__title {
			font-size: 30px;
		}

		&__text {
			font-size: 22px;
		}

		&__picture img {
			max-width: 200px;
		}

		&__counter {
			top: 420px;
		}

		&__nav-item::after {
			left: 0;
			right: auto;
			width: 95px;
		}
	}

	.about {
		&__center {
			max-width: 100%;
			margin-bottom: 0;
		}

		&__picture {
			width: 100%;
			padding-left: 15px;
			padding-right: 15px;
		}

		&__content {
			margin-right: 0;
			padding-left: 15px;
			padding-right: 15px;
		}

		&__container {
			padding-left: 0;
			padding-right: 0;
			padding-bottom: 35%;
		}
	}

	.about-pictures {
		position: relative;
		left: 0;
		top: 0;
		bottom: auto;
		width: 100%;

		.about-pictures__item {
			opacity: 0;
			left: 0;
			top: 0;
			bottom: auto;
			border: 5px solid #fff;

			img {
				object-fit: cover;
				max-height: 350px;
				width: 100%;
			}
		}
	}

	.services {
		padding-top: 40px;
		padding-bottom: 40px;

		&__title {
			padding-bottom: 20px;
			margin-bottom: 40px;
		}
	}

	.gallery {
		.gallery__item {
			width: 100%;

			&:first-child {
				.gallery__title {
					top: 5px;
					left: 5px;
				}
			}

			&:nth-child(2) {
				.gallery__title {
					bottom: 5px;
					right: 5px;
				}
			}

			&:nth-child(3) {
				.gallery__title {
					top: 5px;
					right: 5px;
				}
			}

			&:nth-child(4) {
				.gallery__link {
					bottom: 5px;
					right: 5px;
				}
			}
		}

		.gallery__title {
			font-size: 20px;
		}
	}

	.blog {
		padding-bottom: 80px;
		&__link {
			left: 50%;
			right: auto;
			transform: translateX(-50%);
			max-width: 250px;
			width: 100%;
		}
	}

	.blog-slider {
		margin-right: 0;
		padding-top: 350px;
		padding-bottom: 50px;
		padding-left: 0;

		&__counter {
			left: 0;
			top: -40px;
		}

		&__item {
			max-width: 576px;
			padding: 23px 10px 23px;
			margin-right: 0;
			height: auto;
		}

		&__title {
			font-size: 22px;
			text-align: center;
		}

		&__text {
			font-size: 14px;
			margin-bottom: 10px;
		}

		&__nav {
			right: 0;
		}

		&__nav-item {
			width: 100%;
			height: 350px;
		}

		.slick-prev {
			left: auto;
			right: 100px;
		}

		.slick-next {
			left: auto;
			right: 15px;
		}

		.slick-arrow {
			top: -45px;
		}
	}
	.photo {
		padding-top: 80px;
		&-container {
			padding-left: 0;
			padding-right: 0;
		}
		&__title {
			margin-left: 15px;
		}
	}

	.photo-slider {
		background-size: cover;
		&__counter {
			left: 20px;
			margin-left: 0;
			transform: translateX(0);
		}
		&__title {
			color: #fff;
		}
		.slick-arrow {
			top: 55px;
			left: auto;
			transform: translateX(0);
		}
		.slick-prev {
			right: 80px;
		}
		.slick-next {
			right: 30px;
		}
		.photo-slider__item {
			padding: 0 20px;
			img {
				height: 290px;
			}
			&.slick-current img {
				transform: translateY(0);
				max-width: 100%;
			}
		}
	}
	.footer {
		&-nav {
			display: block;
			&__item {
				margin-bottom: 30px;
			}
		}
		&-bottom {
			&__container {
				display: block;
			}
			&__text {
				margin-bottom: 20px;
			}
		}
	}
}

@media screen and (max-width: 375px) {
}
