html {
   box-sizing: border-box;
}

*,
*::before,
*::after {
   box-sizing: inherit;
}

body {
   font-family: "Akrobat", sans-serif;
   font-weight: 600;
   background: var(--color-bg);
   color: var(--color-dark);
   &.lock {
      overflow: hidden;
   }
}

img {
   max-width: 100%;
}

a {
   text-decoration: none;
   color: inherit;
   outline: none;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
   margin: 0;
   padding: 0;
}

ul {
   list-style: none;
   margin: 0;
   padding: 0;
}

button {
   border: none;
   background: transparent;
   cursor: pointer;
   outline: none;
}

input {
   outline: none;
}

.is-outer {
   position: relative !important; // stylelint-disable-line declaration-no-important
   left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
   overflow: hidden;
}

.is-hidden {
   display: none !important; // stylelint-disable-line declaration-no-important
}

.error {
   border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.container {
   max-width: 1730px;
   padding-left: 15px;
   padding-right: 15px;
   width: 100%;
   margin: 0 auto;
   position: relative;
}

.slick-arrow {
   position: absolute;
   font-size: 0;
   background: none;
   border: none;
   cursor: pointer;

   &:after,
   &:before {
      display: none;
   }
}

.slick-initialized .slick-slide {
   min-height: 0;
}

.slick-slider {
   margin-bottom: 0;
}

.scroll {
   display: block;
   position: absolute;
   transform-origin: 100% 100%;
   transform: rotate(-90deg);
   font-weight: 600;
   font-size: 18px;
   line-height: 20px;
   letter-spacing: 0.06em;
   text-align: right;
   right: 18px;

   &:before {
      content: "";
      display: block;
      position: absolute;
      left: 0;
   }
}

.link {
   display: inline-flex;
   justify-content: center;
   align-items: center;
   font-weight: 600;
   font-size: 18px;
   line-height: 20px;
   letter-spacing: 0.06em;
   text-transform: uppercase;
   padding: 13px 40px;

   svg {
      margin-right: 10px;

      path {
         stroke: var(--color-dark);
         fill: var(--color-dark);
      }
   }
}

.title {
   font-weight: 600;
   font-size: 42px;
   line-height: 130%;
   max-width: max-content;
   padding-bottom: 17px;
   text-transform: uppercase;
   color: var(--color-dark);
   position: relative;

   &:after {
      content: "";
      display: block;
      position: absolute;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 2px;
      background: var(--color-dark);
   }

   span {
      color: var(--color-gray);
   }
}

.counter {
   font-weight: 600;
   font-size: 22px;
   line-height: 20px;
   position: absolute;
   transform-origin: 0 0;
   text-transform: uppercase;

   &__right {
      font-size: 16px;
      color: var(--color-gray);
   }
}
